/*! form */
@media (max-width:$medium-devices) {
	.selectbox{
		height: 50px;
		select{
			padding-left: 20px;
			vertical-align: top;
			font-size: 16px;
		}
		&-group{
			padding-top: 15px;
		}
	}
	.radio{
		display: block;
		margin: 5px 0;
		input[type="radio"] + label {
			display: inline-block;
			width: 100%;
			box-sizing: border-box;
			&:before{
				top: 1px;
			}
			&:after{
				width: 10px;
				height: 10px;
				left: 5px;
			}
		}
	}
	.checkbox{
		display: inline-block;
		min-width: 49%;
		margin: 3px 0;
		input[type="checkbox"]:checked + label:after{
			top: 5px;
			left: 5px;
			width: 10px;
			height: 10px;
		}
	}
	.addfile-delete{
		input{
			vertical-align: middle;
		}
	}
	.input-box textarea,
	.input-box input{
		height: 50px;
		padding: 10px 20px;
	}
	.form{
		.input-inline,
		.selectbox-inline{
			width: 100%;
			margin: 3px 0;
		}
		.input-inline.input-number{
			display: table-cell;
		}
	}
	.number-count {
		clear: both;
		float: none;
		.input-inline{
			width: 100px;
		}
	}
	.input-upload{
		margin-bottom: 50px;
		.input-box{
			height: 50px;
		}
		.value{
			top: auto;
			bottom: -50px;
			padding: 0;
		}

	}
	.input-group{
		.btn{
			margin-left: 0;
		}
	}
	.include-button-group{
		.input-inline{
			width: calc(100% - 145px);
			margin: 0;
			box-sizing: border-box;
			& + .btn{
				float: right;
				width: 140px;
			}
		}
	}
	.calendar{
		display: flex;
		align-items: center;
		.datepicker.input-box{
			flex: 1 1 auto;
			max-width: none;
			padding: 0 5px;
			input{
				padding-right: 5px;
			}
		}
		input{
			max-width: none;
		}
	}

	.activity-time{
		position: relative;
		padding: 40px 0 10px;
		border-top: 1px dashed #ddd;
		& > .inner{
			position: relative;
			padding-top: 40px;
			.datepicker{
				padding: 0;
			}
		}
		&:first-child{
			padding-top: 0;
			border-top: none;
		}
		.calendar{
			float: left;
			width: 45%;
			.datepicker.input-box{
				padding: 0 5px 0 0;
			}
		}
		.number-count{
			clear: none;
			float: left;
			width: 55%;
		}
		.btn-plus,
		.btn-minus{
			position: absolute;
			top: 10px;
		}
		.btn-plus{
			right: 40px;
		}
		.btn-minus{
			right: 0;
		}
		.btn-volunteer{
			width: 100%;
			margin-top: 3px;
			margin-left: 0;
		}
		& ~ .activity-time{
			padding-top: 10px;
		}
	}

}
@media (max-width:$small-devices) {
	.input-box textarea,
	.input-box input{
		//padding: 10px 5px;
	}
	.include-button-group{
		.input-inline{
			width: calc(100% - 105px);
			& + .btn{
				width: 100px;
				padding: 0;
			}
		}
	}

	.input-upload{
		padding-right: 0;
		.input-box{
			input{
				height: 100%;
			}
		}
		.btn{
			position: static;
			width: 100%;
			margin-top: 5px;
		}
	}

	//.selectbox select{
	//	padding-left: 5px;
	//}

	.calendar{
		label{
			display: none;
		}
		.datepicker.input-box{
			padding: 0 2px;
			&:after{
				display: none;
			}
		}
		.btn{
			padding: 0 20px;
		}
	}
	.activity-time{
		.calendar{
			float: none;
			width: 100%;
		}
		.number-count{
			clear: both;
			float: none;
			width: 100%;
		}
	}
}