
/*! common */
@media (max-width:$medium-devices) {
	.is-mobile{
		display: block;
	}
	.is-pc {
		display: none;
	}

	.no-js {
		.tab-menu,
		.gnb-wrap {
			opacity: 0.5;
			pointer-events: none;
			transition: opacity 0.3s;
		}
	}

	h5{
		padding-left: 20px;
		padding-right: 20px;
		box-sizing: border-box;
	}

	.inform-msg{
		margin: 40px 20px 0;
		padding: 0 0 15px;
		font-size: 16px;
		em{
			padding-left: 18px;
		}
	}

	.section-sub-visual{
		display: table;
		width: 100%;
		height: 150px;
		.bg-sub	{
			display: none;
		}
		> .inner{
			display: table-cell;
			vertical-align: middle;
		}
		h2{
			padding-top: 0;
			font-size: 29px;
		}
		small{
			padding: 10px 10px 0;
			line-height: 1.3;
			font-size: 15px;

		}
		.location{
			display: none;
		}
	}

	.group{
		margin: 0 20px 60px;
		&-line{
			padding-bottom: 17px;
			margin-bottom: 30px;
		}
	}

	.heading{
		h3{
			padding-left: 10px;
			font-size: 22px;
			line-height: 1.1;
			time{
				position: absolute;
				top: 25px;
				right: 0;
				font-size: 15px;
				font-weight: 400;
			}
		}
		time{
			float: right;
			margin-top: -20px;
			font-size: 15px;
			letter-spacing: -1px;
		}
		&-etc{
			clear: both;
			float: none;
			.calendar{
				padding-top: 15px;
			}
		}
	}

	.infobox{
		padding: 35px 10px 10px;
		font-size: 15px;
		line-height: 1.5;
	}

	.search-wrap{
		margin: 20px;
		@include clear;
		.tab-menu{
			margin-left: 0;
			margin-right: 0;
			margin-bottom: 50px;
		}
		.selectbox{
			display: block;
			width: 100%;
			margin-bottom: 10px;
		}
		.input-box {
			width: calc(100% - 80px);
			float: left;
			input{
				width: 100%;
			}
		}
		.btn-search{
			float: right;
			width: 70px;
		}
	}

}
@media (max-width:$small-devices) {
	.section-sub-visual{
		small{
			//padding-top: 5px;
			//font-size: 13px;
			letter-spacing: -1px;
		}
	}

	.heading{
		h3{
			font-weight: 700;
			font-size: 17px;
			time{
				top: 0;
				display: block;
				margin-top: 5px;
				font-size: 14px;
			}
		}
		time{
			font-size: 14px;
		}
		&-etc{
			clear: both;
			time{
				clear: both;
				float: none;
				margin-top: 5px;
				padding-left: 10px;
			}
		}
	}
}