/*! tab */
@media (max-width:$medium-devices) {
	.tab {
		&-menu {
			position: relative;
			border: 0;
			height: 65px;
			margin: {
				right: 20px;
				//bottom: 50px;
				bottom: 0;
				left: 20px;
			};
			z-index: 1;
			&:after{
				content: '';
				position: absolute;
				top: 50%;
				right: 15px;
				display: inline-block;
				width: 15px;
				height: 15px;
				border-top: 3px solid #0061a0;
				border-right: 3px solid #0061a0;
				transform: translateY(-50%) rotate(135deg);
				transition: transform 0.3s;
				box-sizing: border-box;
				pointer-events: none;
			}
			ul {
				position: absolute;
				top: 0;
				left: 0;
				overflow: hidden;
				display: block;
				width: 100%;
				max-height: 0;
				padding: {
					top: 65px;
				}
				border-bottom: 1px solid #ddd;
				transition: max-height 0.3s;
			}
			li {
				display: block;
				height: 65px;
				margin-bottom: -1px;
				border: 1px solid #ddd;
				text-align: left;
				background-color: #eee;
				&:first-child{
					border-left: 1px solid #ddd;
				}
				&.active {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					padding: 0;
					display: block;
					border: 1px solid #0061a0;
					background-color: #fff;
					a {
						padding: 0 40px 0 15px;
						font-size: 22px;
						font-weight: 700;
						color: #0061a0;
					}
				}
				&.is-mobile{
					display: block;
				}
				&.two-line {
					padding: 0;
				}
				a {
					padding: 0 15px;
					color: #0061a0;
					line-height: 65px;
					text-overflow: ellipsis;
					white-space: nowrap;
					overflow: hidden;
					font-size: 18px;
					br {
						display: none;
					}
					&.line-wrap{
						line-height: 65px;
					}
				}
			}
			&.show {
				z-index: 1;
				&:after{
					transform: rotate(-45deg);
				}
				ul {
					max-height: 300px;
				}
				li {
				}
			}
		}
	}
}