/*! footer */
@media (max-width: 1200px) {
	.footer {
		&-award{
			.icon{
				position: static;
				margin-top: 0;
			}
			span{
				display: none;
			}
			&.tp-2 {
				position: absolute;
				top: 42px;
				right: -53px;
			}
		}
	}
}

@media (max-width: 1023px) {
	.footer {
		&-award {
			&.tp-2 {
				position: absolute;
				top: 51px;
				right: 282px;
			}
		}
	}
}
@media (max-width:$medium-devices) {
	.footer {
		position: relative;
		padding-top: 0;
		border-top: 1px solid #ccc;
		.family {
			width: 50%;
			margin: 0;
			border-right: 1px solid #ccc;
			label {
				display: none;
			}
			.selectbox {
				font-size: inherit;
				width: 100%;
				height: 82px;
				border: 0;
				background-color: transparent;
			}
			ul {
				right: -1px;
			}
		}
		.sns {
			top: 132px;
			right: 20px;
		}
		.link-list {
			font-size: 15px;
			.label {
				border: 0;
			}
			&.active {
				ul {
					width: auto;
					min-width: 100%;
					margin-top: 1px;
					border-width: 0 1px 1px;
				}
			}
		}
		&-menu-list {
			display: none;
		}
		&-site {
			margin-bottom: 0;
		}
		&-info {
			padding: {
				top: 50px;
				right: 20px;
				bottom: 50px;
				left: 20px;
			};
		}
		&-logo {
			float: none;
			height: 28px;
			margin: 0;
			&:after {
				content: '';
				vertical-align: bottom;
				display: inline-block;
				height: 100%;
			}
			img {
				vertical-align: bottom;
				display: inline-block;
				max-width: 70%;
				max-height: 100%;
			}
		}
		&-etc {
			font-size: 15px;
			letter-spacing: -0.5px;
			float: none;
			margin-top: 23px;
			span {
				display: block;
			}
			.link-list {
				position: absolute;
				top: 0;
				right: 0;
				width: 50%;
				&:before,
				&:after{
					content: '';
					position: absolute;
					right: 20px;
					display: block;
					width: 0;
					height: 0;
					border-style: solid;
				}
				&:before{
					bottom: 50%;
					margin-bottom: 1px;
					border-width: 0 6px 8px 6px;
					border-color: transparent transparent #535353 transparent;
				}
				&:after{
					top: 50%;
					margin-top: 1px;
					border-width: 8px 6px 0 6px;
					border-color: #535353 transparent transparent transparent;
				}
				ul {
					display: none;
				}
				li {
					display: block;
					float: none;
					margin-right: 0;
					a {
						color: inherit;
						line-height: 40px;
					}
				}
				.label {
					display: block;
					height: 82px;
				}
				&.active {
					ul {
						left: auto;
						right: 0;
					}
				}
			}
		}
		&-award{
			position: absolute;
			top: 132px;
			right: 85px;
			margin-top: 3px;
			&.tp-2 {
				position: initial;
				margin-top: 10px;
			}
		}
	}
}