/*! button */
@media (max-width:$medium-devices) {
	.btn-group{
		display: flex;
		justify-content: space-between;
		margin: 30px 0;
		.btn{
			flex: 1 1 auto;
			max-width: 48%;
			margin: 0 auto;
			padding: 0;
		}
	}
	.btn-group-justified{
		padding: 0 20px 20px;
		.pull-right{
			float: none;
			text-align: center;
			.btn{
				width: 100%;
				max-width: 48%;
			}
		}
	}
	.btn{
		font-size: 16px;
		height: 50px;
		&-plus,
		&-minus {
			width: 26px;
			height: 26px;
			padding: 0;
			line-height: 0;
		}
		&-review,
		&-auth{
			width: auto;
			min-width: 75px;
			height: 25px;
			padding: 0 6px;
			margin: 0 9px 0 0;
			font-size: 13px;
			line-height: 25px;
			box-shadow: none;
		}
		&-gotop{
			position: fixed;
			bottom: 20px;
			right: 20px;
			width: 65px;
			height: 65px;
			padding: 0;
			border: 2px solid #199bde;
			box-sizing: border-box;
			box-shadow: none;
			border-radius: 50%;
		}
		&.btn-wide{
			display: flex;
			width: 100%;
		}
	}
}