/*! layer */
@media (max-width:$medium-devices) {
	.layer-wrap{
		table.dataTable tbody th,
		table.dataTable tbody td{
			vertical-align: middle;
		}
		.dataTables_paginate{
			.paginate_button{
				&.first,
				&.last{
					display: none;
				}
			}
		}
	}
	.layer-box{
		top: 0;
	}
	.layer-dim{
		opacity: 1;
		background-color: #fff;
	}
	.layer-partner{
		.layer-box{
			width: 100%;
			padding: {
				top: 54px;
				right: 20px;
				bottom: 20px;
				left: 20px;
			};
			margin-bottom: 0
		}
		.layer-head{
			margin-bottom: 40px;
			h1{
				font-size: 34px;
			}
		}
		.layer-search{
			@include clear;
			.input-inline{
				float: left;
				width: calc(100% - 80px);
			}
			.btn-search {
				float: right;
				width: 70px;
			}
			.label{
				@include clear;
				margin-top: 15px;
			}
		}
		.layer-sort{
			.label{
				float: left;
				margin-bottom: 10px;
			}
			.selectbox{
				float: left;
				width: 100%;
				margin-bottom: 5px;
			}
		}
		.table-thead > ul{
			padding-top: 0;
		}
		.layer-partner-list{
			dt{
				width: 100%;
				box-sizing: border-box;
			}
			dd{
				padding-top: 10px;
			}
		}
	}
	.layer-table{
		table{
			width: 100%;
		}
		colgroup,
		thead{
			display: none;
		}
		tbody{
			display: block;
			tr{
				position: relative;
				display: block;
				padding: 25px 0;
				border-bottom: 1px solid #ccc;
			}
			td{
				position: relative;
				display: block;
				margin: 5px 0;
				padding: 0 0 0 150px;
				border-bottom: none;
				text-align: left;
				font-size: 15px;
				color: #888;

				&:first-child{
					position: absolute;
					top: 50%;
					left: 0;
					display: block;
					width: auto;
					margin-top: -10px;
					padding: 0;
					font-size: 0;
					z-index: 1;
				}
				&:before{
					content: attr(data-title);
					display: inline-block;
					width: 100px;
					margin-left: -100px;
					font-weight: 700;
					color: #000;
				}
				.btn-link{
					height: auto;
					margin: 0;
					padding: 0
				}
				&.empty{
					&:first-child{
						position: static;
						margin: 0;
						text-align: center;
						font-size: 15px;
					}
					.empty-content{
						position: static;
						height: auto;
					}
				}
			}

		}
		.checkbox{
			margin: 0 15px;
			input[type="checkbox"] + label{
				padding-left: 0;
			}
		}
	}
}

@media (max-width:$small-devices) {

	.layer-table{
		tbody{
			td{
				padding: 0 0 0 120px;
				&:before{
					width: 80px;
					margin-left: -80px;
				}
				.checkbox{
					margin: 0 10px;
				}
			}
		}
	}

}