/*! login */
@media (max-width:$medium-devices) {
	.section-login{
		padding-top: 50px;
		padding-bottom: 50px;
		& > .inner{
			width: auto;
			padding: 0 20px;
		}
		.bg{
			width: 283px;
			height: 283px;
			margin-bottom: 23px;
			background-size: 100% 100%;
			&-employees{
				background-image: url('../images/m-login-employees.png');
			}
			&-agents{
				background-image: url('../images/m-login-agents.png');
			}
			&-search-pw{
				background-image: url('../images/m-search-pw.png');
			}
		}
		&.login-gate{
			padding-top: 30px;
			.login-type{
				width: 100%;
				padding: 50px 0;
				&:after{
					display: none;
				}
				&.type-employees{
					margin-right: 0;
					margin-bottom: 20px;
				}
				&.type-agents{
					margin-left: 0;
				}
				.btn{
					width: 215px;
					height: 50px;
				}
			}
		}
		.login-wrap{
			padding: 50px 0 45px;
			.bg{
				float: none;
				width: 282px;
				height: 295px;
				margin: {
					top: 0;
					right: auto;
					bottom: 15px;
					left: auto;
				};
				&-search-pw{
					width: 283px;
					height: 283px;
					background-image: url('../images/m-search-pw.png');
				}
			}
			.title{
				display: block;
				margin-bottom: 20px;
				text-align: center;
				font-size: 27px;
			}
		}
		.login-box{
			float: none;
			max-width: 360px;
			margin: 0 auto;
			padding: 0 20px;
			box-sizing: border-box;
			.login-form{
				margin-bottom: 30px;
			}
			.login-input{
				float: none;
				margin-right: 0;
				.input-box{
					input{
						width: 100%;
					}
				}
			}
			.id-save{
				position: static;
				margin: 10px 0;
				input[type="checkbox"]{
					& + label:before{
						top: -3px;
					}
					&:checked + label:after{
						top: 3px;
					}
				}
			}
			.btn-login{
				float: none;
				width: 100%;
				height: 50px;
				margin-top: 10px;
			}
			.login-info{
				margin-top: 0;
				margin-bottom: 20px;
			}
			.btn-link{
				padding-left: 0;
				margin-right: 10px;
			}
		}
		.search-pw-box{
			.login-input{
				.input-box{
					display: table;
					width: 100%;
					input{
						display: table-cell;
					}
				}
			}
			label{
				float: none;
				display: table-cell;
				width: auto;
				min-width: 100px;
				padding-right: 10px;
				box-sizing: border-box;
			}
			.btn{
				width: 100%;
				margin-left: 0;
			}
		}
	}

	.section-terms {
		padding: 0 15px;
		& > .innter{
			font-size: 16px;
		}
		h4{
			padding-top: 0;
		}
	}
}
@media (max-width:$small-devices) {
	.section-login{
		.bg,
		.login-wrap .bg{
			width: 236px;
			height: 236px;
		}
		.login-wrap{
			.title{
				font-size: 24px;
				letter-spacing: -.5px
			}
		}
		.search-pw-box{
			label {
				font-size: 13px;
				min-width: 90px;
				letter-spacing: -1px;
			}
		}
	}
}
@media (max-width:$extra-small-devices) {
	.section-login{
		&.login-gate{
			.login-text{
				strong{
					display: block;
				}
			}
		}
	}
}