/*! program */
@media (max-width:$medium-devices) {
	.program{
		&-img{
			position: relative;
			width: 100%;
			height: auto;
			padding-bottom: 50%;
			span {
				position: absolute;
				left: 0;
				top: 0;
				right: 0;
				bottom: 0;
				white-space: nowrap;
				overflow: hidden;
				img {
					position: absolute;
					top:50%;
					left:50%;
					transform: translate(-50%, -50%);
					object-fit: fill;
				}
				&:after {
					content: '';
					display: inline-block;
					vertical-align: middle;
					height: 100%;
				}
			}
		}

		&-info{
			height: auto;
			padding: 30px 10px 0;
		}

		&-title{
			font-size: 34px;
			line-height: 1.2;
			strong{
				white-space: normal;
			}
		}

		&-state{
			margin-bottom: 30px;
			font-size: 15px;
			line-height: 2;
			strong {
				line-height: 1.6;
			}
		}

		&-list{
			.group{
				padding: 25px 0;
			}
			.program{

				&-info{
					padding: 0 10px 0;
				}
				&-category{
					padding: 20px 0 0;
				}
				&-title{
					padding-top: 25px;
					margin-bottom: 10px;
					strong{
						display: block;
						max-height: 82px;
						white-space: normal;
						overflow: hidden;
						word-break: break-all;
						text-overflow: ellipsis;
						display: -webkit-box;
						-webkit-line-clamp: 2;
						-webkit-box-orient: vertical;
					}
					.category{
						margin-bottom: 10px;
					}
				}
				&-desc{
					display: block;
					//height: 48px;
					margin-bottom: 10px;
					overflow: hidden;
					word-break: break-all;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
				}
				.btn{
					position: static;
					width: 50%;
					margin-top: 40px;
				}
			}
		}
	}
	.finance{
		.item{
			margin: 50px 0 50px;
		}
		h4 {
			font-size: 22px;
		}
		.elements{
			padding: 20px 10px 0;
			li{
				margin-bottom: 25px;
				color: #333;
				strong{
					width: 25%;
				}
				span {
					display: inline-block;
					width: 72%;
					vertical-align: top;
				}
				.flag{
					position: absolute;
					border-width: 7.5px;
				}
			}
		}
		.standards{
			margin-top: 35px;
			li{
				float: left;
				width: 182px;
				color: #333;
				&:last-child{
					width: 149px;
				}
				.icon{
					display: block;
					width: 148px;
					height: 148px;
					border-radius: 50%;
				}
				strong{
					display: block;
					padding-top: 25px;
					padding-left: 30px;
				}
				span{
					display: block;
					padding-top: 10px;
					padding-left: 30px;
					line-height: 1.3;
				}

			}
		}
	}

	.box{
		margin: 30px 0;
		& > .inner{
			padding: 0 20px;
			box-sizing: border-box;
		}
		h3{
			margin-bottom: 25px;
			font-weight: 700;
			font-size: 24px;
			color: #333;
		}
		h4 {
			margin-bottom: 25px;
			padding: 0;
			font-weight: 700;
			font-size: 20px;
			color: #333;
		}
		.con{
			margin-bottom: 20px;
			padding: 0 10px;
			line-height: 1.5;
			color: #333;
			box-sizing: border-box;
			word-break: break-all;
		}
		.btns{
			text-align: center;
			.btn {
				&:only-child {
					 width: 100%;
				}
			}
		}
	}

}

@media (max-width:$small-devices) {

	.program{
		&-state{
			font-size: 14px;
			letter-spacing: -.5px;
			strong{
				width: 60px;
			}
		}
		&-list{
			.program{
				&-title{
					//height: 60px;
					font-size: 24px;
					strong{
						max-height: 56px;
					}
				}
				.btn{
					position: static;
					width: 50%;
					margin-top: 40px;
				}
			}
		}
	}

	.finance{
		.elements{
			li{
				strong{
					width: 35%;
				}
				span {
					width: 60%;
				}
			}
		}
	}
}