/*! mypage */
@media (max-width:$medium-devices) {
	.mypage-wrap{
		.tab-menu{
			margin-top: 15px;
		}
		.search-wrap{
			margin-bottom: 0px;
		}

	}
	.search-period{
		@include clear;
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin: 0;
		padding-top: 20px;
		.btn{
			flex: 1 1 auto;
		}
	}
	.mypage-contents{
		padding-top: 50px;
		.heading{
			.pull-right{
				width: 100%;
			}
			h3{
				font-size: 18px;
				time{
					font-size: 16px;
				}
			}
			time{
				font-size: 16px;
			}
		}
		.list ul li{
			padding: 20px;
		}
		.group{
			margin-bottom: 30px
		}
	}
	.my-program{
		.tab-btns{
			display: flex;
			justify-content: space-between;
			margin: 0 20px 15px;
			.btn{
				flex: 1 1 auto;
			}
		}
		.program-wrap{
			padding: 25px 0 0;
			margin: 0 20px;
		}
		.program-content{
			margin: 0 20px 75px;
			padding: 30px 10px;
			font-size: 15px;
		}
		.board-detail{
			.detail-summary{
				padding: 30px 10px;
			}
		}
	}

	.mypage-summary{
		margin: 0 10px;
		li{
			float: none;
			width: 100%;
			margin: 0;
			padding: 20px 10px;
			text-align: left;
			font-size: 15px;
			border-bottom: 1px solid #f2f2f2;
			&:before{
				display: none;
			}
			.count,
			.detail{
				text-align: right;
			}
			.detail{
				span{
					line-height: 1.2;
				}
			}
			.count{
				padding-top: 0;
				strong{
					font-size: 33px;
				}
			}
		}
	}

	.partner-list{
		li{
			strong{
				width: 33.5%;
			}
			div{
				padding-left: 10px;
			}
		}
	}

	.vms{
		&-introduce,
		&-join,
		&-auth{
			& > .inner{
				width: auto;
				margin: 0 20px;
			}
		}
		&-introduce{
			h3{
				margin-bottom: 20px;
				padding-top: 30px;
				font-size: 22px;
			}
			.con{
				font-size: 15px;
			}
		}
		&-join{
			& > .inner{
				padding-top: 30px;
				padding-bottom: 35px;
			}
			h4{
				font-size: 18px;
			}
			.con{
				margin-bottom: 30px;
			}
			.howto{
				& > p{
					float: none;
					width: 135px;
					height: 135px;
					margin: 0 auto;
					border-width: 4px;
					em{
						font-size: 18px;
					}
				}
				.join-step{
					margin-left: 0;
					padding-top: 25px;
					li{
						margin-top: 20px;
						font-size: 15px;
						strong{
							display: block;
							width: 100%;
							margin-bottom: 5px;
						}
					}
				}
			}
			.btn-group{
				margin-bottom: 0;
				.btn{
					flex: 0 1 43%;
					margin: 0 auto;
					padding: 0;
				}
			}
		}
		&-auth{
			margin-bottom: 50px;
			.table .th{
				padding-top: 33px;
			}
			.btn-group{
				margin-bottom: 0;
				.btn{
					flex: 0 1 43%;
					margin: 0 auto;
					padding: 0;
				}
			}
		}
	}

	.budget-content{
		margin-bottom: 35px;
		.table-vertical{
			padding-bottom: 0;
			border-bottom: none;
			& > ul{
				li{
					padding: {
						top: 17px;
						right: 0;
						bottom: 15px;
						left: 80px;
					};
					> *{
						// display: flex;
						// align-items: center;
						padding-top: 0;
						padding-bottom: 0;
					}
					& > div:first-child{
						padding-left: 10px;
					}
					& > div:before,
					&:first-child > div:before{
						width: 80px;
						//margin-left: -80px;
						padding-left: 10px;
						box-sizing: border-box;
					}
				}
				.input {
					&-box {
						padding-right: 0;
					}
				}
			}
			.tfoot{
				& > div:first-child{
					position: absolute;
					//float: left;
					width: 80px;
					margin-left: -80px;
					padding: 16px 0 16px 10px;
					box-sizing: border-box;
				}
			}
		}
		.input-group{
			& > *{
				display: block;
			}
			.input-box{
				margin-bottom: 5px;
			}
			.btn{
				float: right;
			}
		}
		& + .btn-group .btn{
			flex: 0 1 45%;
			margin: 0 auto;
		}
		&.budget-write{
			.table-vertical > ul li > div:before{
				padding-top: 13px;
				align-items: start;
			}
		}
	}
}

@media (max-width:$small-devices) {
	.partner-list{
		li{
			padding: 0 10px;
			margin-top: 20px;
			&:first-child{
				margin-top: 0;
			}
			strong,
			div{
				display: block;
				width: 100%;
				padding: 0px;
			}
			strong{
				margin-bottom: 5px;
			}
		}
	}

}