/*! join */
@media (max-width:$medium-devices) {
	.section-join{
		& > .inner{
			width: auto;
			padding: 0 20px;
		}
		.join-steps{
			ol{
				margin-top: 46px;
				margin-bottom: 70px;
			}
			li{
				.step-number{
					width: 41px;
					height: 41px;
					line-height: 34px;
					font-size: 22px;
				}
			}
		}
		.group{
			margin: {
				right: 0;
				left: 0;
			};
		}
		.group-terms{
			margin: {
				top: 75px;
				right: 0;
				bottom: 35px;
				left: 0;
			};
			.contents{
				font-size: 12px;
			}
			.term-agree{
				top: 0;
			}
		}
		.confirm-wrap{
			margin: 0;
		}
	}
}
@media (max-width:$small-devices) {
	.section-join{
		.group-terms{
			.term-agree {
				top: -3px;
			}
		}
	}
}