@media (max-width:$medium-devices) {
	.section-result{
		padding-top: 50px;
		h2{
			font-size: 40px;
			time{
				margin-top: 10px;
				font-size: 15px;
			}
		}
		.content{
			background-image: none;
			ul{
				width: 100%;
				margin: 45px auto 0;
			}
			li{
				display: flex;
				flex-direction: row;
				flex-wrap: nowrap;
				width: 100%;
				margin-bottom: 50px;
				padding: 0 20px;
				box-sizing: border-box;
				.title{
					.line{
						height: 4px;
					}
					strong{
						font-size: 25px;
					}
					.count{
						font-size: 50px;
					}
				}
			}
			.volunteering-time{
				margin-bottom: 50px;
				justify-content: flex-end;
				align-content: stretch;
				align-items: flex-end;
				.title{
					position: static;
					padding-right: 20px;
					box-sizing: border-box;
					div {
						display: flex;
						flex-direction: column;
						.count {
							margin: 0;
						}
						strong {
							order: 1;
						}
					}
				}
				.photo{
					margin-right: 0;
					&-1 {
						width: 55%;
					}
				}
			}
			.volunteer{
				flex-direction: column;
				.title{
					position: relative;
					left: 0;
					right: 0;
					top: 0;
					padding-left: 0;
					order: 1;
					.line{
						width: 100%;
						margin-top: 35px;
						vertical-align: top;
					}
					strong{
						position: relative;
						display: inline-block;
						top: -17px;
						padding-left: 20px;
						padding-right: 50px;
						background: #fff;
					}
				}
				.count{
					position: absolute;
					top: -50%;
					right: -15px;
				}
				.photo{
					margin-right: 0;
					width: 100%;
					&-1{
						order: 0;
						margin-bottom: 0;
						img {
							width: 100%;
						}
					}
					&-2{
						order: 2;
						width: 100%;
						padding-right: 56.5px;
						text-align: right;
						box-sizing: border-box;
						& > div{
							display: inline-block;
							width: 34.1%;
						}
					}
				}

			}
			.partner{
				position: relative;
				top: initial;
				right: 0;
				justify-content: flex-start;
				align-content: stretch;
				align-items: flex-start;
				.title{
					position: relative;
					left: inherit;
					order: 1;
					padding-left: 20px;
				}
				.photo{
					&-1{
						width: 62.27%;
					}
				}
			}
			.beneficiary{
				position: relative;
				top: initial;
				right: 0;
				.title{
					right: 45px;
					bottom: 20px;
				}
				.photo {
					width: 100%;
					img {
						width: 100%;
					}
				}
			}
		}

		.result {
			&-biz {
				.slide {
					height: auto;
					h3 {
						font: {
							size: 36px;
						};
						text-align: center;
					}
				}
			}
			&-nav {
				position: relative;
				.slick {
					&-arrow {
						color: #ccc;
						font: {
							size: 24px;
							weight: 700;
						};
						line-height: 100%;
						top: 12px;
						width: auto;
						height: auto;
						border: 0;
						transform: none;
						&:after {
							content: '';
							position: absolute;
							top: 50%;
							box-sizing: border-box;
							display: block;
							width: 12px;
							height: 12px;
						}
					}
					&-prev {
						left: 0;
						padding-left: 25px;
						&:after {
							left: 0;
							border-right: 3px solid;
							border-bottom: 3px solid;
							transform: rotate(135deg) translateX(-50%);
						}
					}
					&-next {
						right: 0;
						padding-right: 25px;
						&:after {
							right: 0;
							margin-right: 6px;
							border-right: 3px solid;
							border-bottom: 3px solid;
							transform: rotate(315deg) translateX(50%);
						}
					}
				}
			}
			li {
				float: none;
			}
		}
	}
}

@media (max-width:$small-devices) {
	.section-result{
		.content{
			li{
				.title{
					.line{
						height: 4px;
					}
					strong{
						font-size: 20px;
					}
					.count{
						font-size: 40px;
					}
				}
			}
			.volunteer{
				.count{
					position: absolute;
					top: -40%;
					right: -5px;
				}
			}
		}
	}
}