/*! board-detail */
@media (max-width:$medium-devices) {
	.board-detail{
		margin: {
			top: 0;
			right: 20px;
			left: 20px;
		};
		.detail-summary{
			padding: 30px 10px;
		}
		.detail-title{
			font-size: 22px;
			word-break: break-all;
		}
		.meta{
			letter-spacing: -.5px;
			& > span{
				display: inline-block;
				font-size: 16px;
				&.meta-file{
					display: block;
					margin-top: 10px;
				}
			}

			&-category:after{
				//display: none;
			}
		}
		.detail-contents{
			margin-bottom: 0;
			padding: 25px 0;
			font-size: 16px;
			p{
				padding: 0;
				font-size: 16px;
			}
		}
	}
}

@media (max-width:$small-devices) {
	.board-detail{
		.meta{
			letter-spacing: -1px;
			&-volunteer-date{
				time:before {
					content: '';
					display: block;
				}
			}
		}
	}
}