/*! main */
@media (max-width:1400px){
	.main-wrap {
		.section {
			&-news {
				.title {
					font-size: 25px;
				}
				.category{
					margin-bottom: 10px;
				}
			}
		}
	}
}

@media (max-width:$medium-devices) { //960
	.main-wrap {
		min-width: 0;
		.slick {
			&-arrow {
				display: none !important;
			}
		}
		.section {
			h2 {
				font: {
					size: 40px;
				};
				white-space: nowrap;
			}
			&-visual {
				padding-bottom: 75%;
				.slide {
					height: 100%;
					&-list {
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
					}
					&-controls {
						bottom: 20px;
						.btn {
							&-play,
							&-pause {
								width: 30px;
								height: 30px;
							}
						}
						.slick-dots {
							padding-left: 34px;
							li {
								width: 30px;
								height: 30px;
								padding-top: 6px;
								button {
									width: 18px;
									height: 18px;
								}
							}
						}
					}
				}
				h2 {
					font-size: 22px;
					white-space: normal;
					width: 90%;
					strong {
						font-size: 46px;
					}
				}
			}
			&-activity {
				height: auto;
				padding: {
					right: 20px;
					bottom: 75px;
					left: 20px;
				};
				.slide {
					height: auto;
					h2 {
						font-size: 38px;
						top: 50px;
						time{
							padding-top: 0;
						}
					}
					.content {
						padding: {
							top: 150px;
							right: 0;
							left: 0;
						};
					}
					&-controls {
						line-height: 1;
						right: 20px;
						bottom: 75px;
						left: auto;
						width: auto;
						transform: none;
					}
					&-paging {
						letter-spacing: -1px;
						strong {
							font-size: 40px;
							display: inline-block;
							height: 36px;
							//border: 1px solid red;
						}
						span {
							font-size: 24px;
							display: inline-block;
							height: 22px;
							//border: 1px solid red;
						}
					}
					&-auto {
						display: inline-block;
						margin-top: 16px;
						margin-bottom: 0;
						vertical-align: bottom;
						.btn-pause:before,
						.btn-pause:after {
							width: 1px;
						}
					}
				}
				.program {
					&-img {
						position: relative;
						width: auto;
						height: 0;
						padding-bottom: 50%;
						span {
							position: absolute;
							width: 100%;
						}
					}
					&-title {
						font-size: 30px;
						max-height: 78px;
						height: auto;
						padding-top: 0;
						strong {
							height: auto;
							max-height: 78px;
						}
					}
					&-list {
						width: auto;
						padding-right: 0;
						&.double {
							li {
								float: none;
								width: auto;
								margin-top: 15px;
								padding-left: 0;
								&:first-child {
									margin-top: 0;
								}
							}
							li + li{
								margin-top: 50px;
							}
							.btn {
								width: 50%;
							}
							.program {
								&-info {
									padding-top: 20px;
								}
								&-title{
									strong{
										height: auto;
										max-height: 78px;
									}
								}
								&-img {
									width: auto;
									height: 0;
									padding-bottom: 50%;
								}
								&-desc {
									margin-bottom: 10px;
								}
							}
						}
					}
					&-desc {
						//height: 47px;
					}
					&-info {
						height: auto !important;
						padding: {
							left: 0 !important;
						}
					}
					&-state {
						margin-bottom: 0;
					}
					.btn {
						line-height: 50px;
						position: static;
						width: 50%;
						height: 50px;
					}
				}
				.volunteer {
					&-activities {
						width: auto;
						padding: 0;
						li {
							width: 100%;
							margin-left: 0;
						}
						img {
							width: 100%;
						}
						.volunteer {
							padding-bottom: 50px;
							.title {
								top: 15px;
								height: 40px;
								padding-top: 33%;
								&:before {
									display: none;
								}
								strong {
									padding-top: 0;
									padding-right: 70px;
									background-color: transparent;
								}
								.count {
									top: 108%;
									right: 0px;
									transform: rotate(90deg) translate(15px, 100%);
									transform-origin: 100% 100%;
								}
							}
							.photo {
								&-lg {
									margin-bottom: 56px;
								}
								&-sm {
									width: auto;
									height: 75px;
									margin-right: 70px;
									& > div{
										height: 100%;
										img{
											width: auto;
											height: 100%;
										}
									}
								}
							}
						}
						.volunteering-time {
							padding-bottom: 100px;
							.title {
								top: 0;
								padding-top: 72%;
								div {
									position: relative;
								}
							}
						}
						.beneficiary {
							.title {
								strong {
									margin-left: 0;
								}
							}
							.photo {
								&-sm {
									width: auto;
									height: 63px;
									img {
										width: auto;
										max-width: 50%;
										max-height: 100%;
									}
								}
							}
						}
					}
				}
			}
			&-news {
				padding: {
					top: 145px;
					right: 20px;
					bottom: 80px;
					left: 20px;
				};
				h2 {
					top: 65px;
				}
				.title {
					top: 7.8%;
					font-size: 18px;
				}
				.category{
					display: inline-flex;
					align-items: center;
					justify-content: center;
					height: 24px;
					line-height: normal;
				}
				.is-pc {
					display: block;
				}
				.is-mobile {
					display: none;
				}

			}
			&-service {
				height: auto;
				padding: 10px 0;
				ul {
					display: block;
					width: auto;
				}
				li {
					position: relative;
					float: left;
					flex: none;
					width: 25%;
					height: auto;
					box-sizing: border-box;
					&:before {
						content: '';
						display: block;
						padding-bottom: 100%;
					}
					a {
						position: absolute;
						top: 50%;
						left: 0;
						width: 100%;
						height: 100%;
						padding-bottom: 0;
						white-space: nowrap;
						transform: translateY(-50%);
						overflow: hidden;
						&:hover {
							background-color: transparent;
						}
						&:after {
							content: '';
							display: inline-block;
							height: 100%;
							vertical-align: middle;
						}
					}
					em {
						position: static;
						display: inline-block;
						width: auto;
						height: auto;
						margin: 0;
						padding: 0;
						vertical-align: middle;
					}
					.icon{
						display: inline-block;
						//height: 40px;
						vertical-align: middle;
						background-position: 50% 100%;
						transform: scale(0.8);
					}
					span {
						min-height: 52px;
						padding-top: 10px;
						line-height: 1.5;
						box-sizing: border-box;
					}
				}
			}
		}
	}

	.main-wrap {
		&,
		& ~ .footer {
			min-width: 0;
		}
	}
}

@media (max-width:767px) {
	.main-wrap {
		.section {
			&-news {
				.news-list {
					&:before {
						display: none;
					}
					li {
						position: relative;
						top: auto;
						bottom: auto;
						left: auto;
						width: 100%;
						padding-bottom: 20px;
						a {
							position: absolute;
							top: 0;
							right: 0;
							bottom: 0;
							left: 0;
							overflow: hidden;
							display: block;
						}
						img {
							width: 100%;
						}
						&.news {
							&-1,
							&-3,
							&-5 {
								.thumb {
									&:before {
										padding-bottom: 87.5%;
									}
								}
							}
							&-2,
							&-4,
							&-6 {
								.thumb {
									&:before {
										padding-bottom: 54.55%;
									}
								}
							}
						}
					}
					.title{
						font-size: 28px;
					}
				}
				.category{
					margin-bottom: 15px;
				}
				.is-pc {
					display: none;
				}
				.is-mobile {
					display: block;
				}
			}
		}
	}
}

@media (max-width:$small-devices) { //480px
	.main-wrap {
		.section {
			&-visual {
				h2{
					strong{
						font-size: 26px;
					}
				}
			}
			&-service{
				li{
					width: 50%;
					&:before {
						padding-bottom: 60%;
					}
				}
			}
		}
	}
}

@media (max-width:$extra-small-devices) { //320px
	.main-wrap {
		.section {
			&-service{
				li{
					&:before {
						padding-bottom: 70%;
					}
				}
			}
		}
	}
}