/*! list */
@media (max-width:$medium-devices) {
	.list{
		ul {
			display: block;
			li{
				display: block;
				padding: 20px 0;
				border-bottom: 1px solid #ccc;
				font-size: 15px;
				& > *{
					display: block;
					padding: 0;
					border-bottom: none;
				}
				.date,
				.period,
				.etc{
					width: 100%;
					text-align: left;
				}
				.date{
					margin-bottom: 10px;
				}
				.period,
				.etc{
					margin-top: 20px;
				}
			}
		}
		.btn-more{
			right: 0;
			top: -50px;
			padding-right: 18px;
			font-weight: 400;
			.icon{
				bottom: 32px;
			}
		}
	}

	.list-card{
		margin-top: 30px;
		.pagination-wrap{
			width: auto;
			margin: 20px auto 0;
		}
	}

	.list-block{
		.table-thead > ul > li,
		.table-thead > ul > li:first-child{
			position: relative;
			padding: 0;
			&:before{
				display: none;
			}
		}
		.table-thead > ul > li{
			*:before,
			&:first-child > *:before{
				display: none;
			}
			.title{
				min-height: auto;
				margin-top: 0;
				margin-bottom: 0;
				word-break: break-all;
				a{
					padding: 55px 10px 30px;
					box-sizing: border-box;
					em{
						height: 58px;
						line-height: 1.6;
						font-size: 18px;
						white-space: normal;
						overflow: hidden;
						word-break: break-all;
						text-overflow: ellipsis;
						display: -webkit-box;
						-webkit-line-clamp: 2;
						-webkit-box-orient: vertical;
					}
				}
			}
			.cell-date{
				position: absolute;
				top: 12px;
				left: 10px;
				width: auto;
				margin-top: 0;
				margin-bottom: 0;
				color: #333;
			}
			.cell-etc{
				display: none;
			}
		}
	}

	.grid{
		width: auto;
		margin: 0 20px;
		&-item{
			width: 100%;
			margin-bottom: 25px;
		}
		.item-box{
			.img{
				width: 100%;
				height: auto;
				&:before {
					content: '';
					display: inline-block;
					width: 100%;
					padding-bottom: 76%;
					vertical-align: middle;
				}
				span{
					width: 100%;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
				}
			}
			.content{
				.category{
					padding: 6px 0;
				}
				.title{
					height: 60px;
					font-size: 22px;
					//line-height: 1.3;
				}
			}
		}
		.item-box-small{
			.img{
				&:before{
					padding-bottom: 69.32%;
				}
			}
		}
	}

	.multi-list{
		li {
			margin-top: 20px;
			.btn ~ .selectbox{
				margin-left: 0;
			}
		}
	}

	.table{
		&-horizontal{
			.th,
			.td{
				padding: {
					top: 15px;
					bottom: 15px;
					left: 10px;
				};
			}
			& > ul{
				& > li{
					font-size: 15px;
					&.odd,
					&.even,
					&.full{
						float: none;
						width: 100%;
						padding-right: 0;
						.th{
							width: 33.5%;
							padding-top: 28px;
						}
						.td{
							width: 66.5%;
						}
					}
					.help,
					.help-block,
					.form .input-inline ~ .help{
						margin-left: 0;
						text-align: left;
						white-space: normal;
					}
					.form .input-number ~ .help{
						display: table-cell;
						padding-left: 10px;
						vertical-align: middle;
					}
				}
			}
		}
		&-vertical{
			& > ul{
				& > li{
					display: block;
					padding: 30px 10px;
					border-bottom: 1px solid #ccc;
					& > *{
						position: relative;
						//display: block;
						//padding: 0;
						border-bottom: none;
						text-align: left !important;
						box-sizing: border-box;
						display: flex;
						align-items: center;
						flex-wrap: wrap;
						padding-top: 0;
						padding-bottom: 0;
						padding-left: 0;
					}
					& > *,
					.cell-addfile,
					.cell-date,
					.cell-time,
					.cell-etc,
					.cell-btn{
						box-sizing: border-box;
						width: 100%;
						min-height: 30px;
						margin-top: 10px;
						margin-bottom: 10px;
					}
					& > div{
						clear: left;
						padding-right: 0 !important;
						//padding-bottom: 10px;
						// display: flex;
						// align-items: center;
						// flex-wrap: wrap;
						&:after{
							content: '';
							display: block;
							clear: both;
						}
						&:first-child {
							//padding-left: 10px !important;
						}
					}
					.cell-btn{
						&:before{
							padding: 3px 0;
						}
						.btn{
							height: 25px;
							margin: 0 3px;
							line-height: 25px;
						}
						em{
							display: inline-block;
							padding: 3px 0;
							vertical-align: middle;
						}
					}
				}
			}
		}
		&-thead {
			& > ul{
				padding-top: 0;
				& > li,
				& > li:first-child{
					padding-left: 115px;
					& > *:before{
						content: attr(data-title);
						// position: static;
						// //display: inline-block;
						// margin-left: -115px;
						// float: left;
						position: absolute;
						top: 0;
						//bottom: 0;
						left: 0;
						width: 115px;
						height: auto;
						min-height: 100%;
						font-weight: 700;
						text-align: left;
						letter-spacing: -0.5px;
						color: #000;
						line-height: normal;
						border-bottom: none;
						box-sizing: border-box;
						display: block;
						padding-top: 0;
						padding-right: 10px;
						transform: translate(-100%, 0);
						display: flex;
						align-items: center;
					}
					.title{
						a{
							float: left;
							width: 100%;
						}
					}
				}
			}
		}
		&-no-heading{
			border-top: 1px solid #000;
		}
		.addfile{
			display: block;
			.th,
			.td{
				display: block;
				width: 100%;
			}
			.th{
				padding: 20px 10px 0;
			}
			.td{
				a{
					position: relative;
					padding-left: 10px;
					word-break: break-all;
					&:before{
						content: '-';
						position: absolute;
						top: 3px;
						left: 0;
					}
				}
			}
		}
		.add-volunteer{
			&-list{
				& > span{
					display: inline-block;
					margin-right: 15px;
					margin-bottom: 10px;
					white-space: nowrap;
				}
				.btn-minus{
					margin-top: -2px;
				}
			}
		}
	}

	.pagination{
		&-wrap{
			margin: 0 auto 75px;
			text-align: left;
		}
		&-list{
			display: inline-block;
			margin: 0 20px;
			li{
				min-width: 35px;
				height: 35px;
				line-height: 35px;
				&:first-child,
				&:last-child {
					display: none;
				}
				&:first-child + li {
					border-left: 1px solid #ccc;
				}
			}
		}
	}
}
@media (max-width:$small-devices) {

	.table{
		&-horizontal{
			& > ul{
				& > li{
					font-size: 13px;
				}
			}
		}

		&-thead {
			& > ul{
				& > li,
				& > li:first-child{
					padding-left: 75px;
					& > *:before{
						width: 75px;
						//margin-left: -75px;
					}
				}
			}
		}
	}
}