@media (max-width:$medium-devices) {
	.finance {
		& > .inner {
			padding: 0 20px;
			box-sizing: border-box;
		}
	}
	.outreach {
		margin-top: 50px;
		padding: 50px 0 15px;
		.inner {
			padding: 0 30px;
			box-sizing: border-box;
		}
		h3 {
			margin-bottom: 15px;
			font-size: 22px;
		}
		.result {
			margin-top: 30px;
			white-space: normal;
			&:before {
				content: none;
			}
			li {
				display: block;
				width: 270px;
				height: 270px;
				margin: {
					top: 35px;
					right: auto;
					left: auto;
					bottom: 35px;
				};
				padding: 45px 0;
				font-size: 25px;
				&:first-child {
					margin-left: auto;
				}
				.icon {
					display: block;
					margin: 0 auto 20px;
				}
				strong {
					display: block;
					font-size: 40px;
					span {
						font-size: 40px;
					}
				}
			}
		}
	}
	.business{
		.program{
			&-wrap{
				padding: 0;
				margin: 30px 20px;
				border-top: 0;
			}
			&-content{
				margin: 0 20px 75px;
			}
		}
	}
	.business-wrap {
		.information {
			h4{
				margin-bottom: 20px;
			}
		}
		.finance.box{
			padding-top: 0;
			h5{
				padding-left:0;
			}
			.item{
				margin-bottom: 25px;
			}
		}
		.slogan {
			display: block;
			margin-top: 50px;
			padding: 35px 10px 50px;
			.keyword{
				display: block;
				width: 100%;;
				margin-bottom: 15px;
				font-weight: 700;
				font-size: 22px;
				text-align: left;
			}
			.mean{
				display: block;
				padding-left: 0;
				font-size: 15px;
			}
		}
		.submain-desc{
			margin-top: 35px;
			margin-bottom: 50px;
			padding-top: 45px;
			word-break: break-all;
			&.bg-circle{
				&:before{
					height: 100%;
					background-size: 100% auto;
				}
			}
			h3{
				font-size: 30px;
				&:after{
					width: 45px;
					margin-left: -22.5px;
				}
			}
			p {
				margin-top: 35px;
				font-size: 16px;
			}
		}
		.headline {
			padding-top: 50px;
			> .inner {
				padding: 0 20px;
				box-sizing: border-box;
			}
			h3 {
				font-size: 28.5px;
			}
			.description {
				padding: 0 10px;
				box-sizing: border-box;
				p {
					margin-top: 25px;
					text-align: left;
					//word-break: break-all;
				}
				&.border {
					padding-bottom: 35px;
					margin-bottom: 20px;
				}
			}
		}
		h4 {
			&.bullet {
				font-size: 22px;
				margin-left: 10px;
				padding: {
					bottom: 0;
					left: 16px;
					top: 0;
				}
				&:before {
					top: 50%;
					width: 8px;
					height: 8px;
					border: 2.5px solid #007abc;
					transform: translateY(-50%);
				}
			}
		}
		.con {
			&.indent {
				margin-left: 0;
			}
		}
		.cards {
			padding: 50px 0 35px;
			.inner {
				padding: 0 20px;
				box-sizing: border-box;
			}
			&.finance {
				a {
					&:active {
						background-color: #def4ff;
					}
				}
			}
			&.community {
				a {
					&:active {
						background-color: #edf5dc;
					}
				}
			}
			h4 {
				margin-bottom: 25px;
				font-size: 22px;
			}
			ul {
				margin: {
					left: -10px;
					right: -10px;
				};
				li {
					width: 50%;
					padding: 10px;
					a {
						min-height: 220px;
						padding: 40px 0;
						box-sizing: border-box;
						span {
							font-size: 18px;
						}
					}
				}
			}
		}
		.education {
			> .inner {
				padding: 0 20px;
				box-sizing: border-box;
			}
			.grade {
				@include clear;
				margin: {
					top: -25px;
					left: 0;
					right: 0;
					bottom: 0;
				}
				li {
					width: 33.33%;
					margin: 15px 0;
					font-size: 22px;
					strong {
						margin-top: 15px;
						font-size: 25px;
					}
				}
			}
			.lesson {
				margin-bottom: 60px;
				.con {
					padding: 15px 10px 0;
					font-size: 16px;
				}
				strong {
					display: block;
					margin-bottom: 10px;
					font-size: 20px;
				}
				figure {
					margin: {
						left: 0;
						right: 0;
					}
				}
			}
			.course {
				padding-top: 25px;
				> li {
					margin-bottom: 25px;
					padding: 0 10px;
					box-sizing: border-box;
				}
				&-title {
					font-size: 18px;
					min-width: 135px;
					width: 135px;
					height: 135px;
					border: {
						width: 4px;
					}
					margin: 0;
				}
				.depth {
					margin: 0;
					padding-left: 35px;
					width: 75%;
					box-sizing: border-box;
					> li {
						width: 100%;
						float: none;
						margin-bottom: 10px;
						font-size: 15px;
					}
					&.even {
						li {
							&:nth-last-child(2){
								margin-bottom: 10px;
							}
						}
					}
				}
			}
		}
		.information {
			.inner {
				padding: 0 30px;
				box-sizing: border-box;
			}
			.notice {
				color: #333;
				> ul {
					> li {
						padding-bottom: 30px;
						strong{
							display: block;
							width: 100%;
							padding-bottom: 15px;
						}
						ul {
							display: block;
							padding-left: 23px;
							&.list-inline {
								max-width: none;
								li {
									padding: 3px 0 0 10px;
								}
							}
							li {
								position: relative;
								padding-left: 10px;
								&:before {
									position: absolute;
									left: 0;
									top: 0;
								}
							}
						}
					}
				}
			}
			.plan {
				padding-top: 10px;
				> span {
					position: static;
					width: 109px;
					height: 109px;
					margin: 0 auto;
					line-height: 109px;
					font-size: 22px;
				}
				ul {
					@include clear;
					position: relative;
					margin: {
						top: 0;
						left: 0;
						bottom: 35px;
					};
					padding-top: 50px;
					&:before {
						content: '';
						display: block;
						position: absolute;
						top: 0;
						left: 50%;
						background-color: #48afde;
						border: none;
						border-radius: 0;
						width: 2px;
						height: 100%;

					}
				}
				li {
					position: relative;
					float: none;
					width: 50%;
					height: auto;
					margin-top: 50px;
					padding: 0;
					font-size: 15px;
					&:before {
						top: 4px;
						left: initial;
						width: 19px;
						height: 19px;
					}
					&:nth-child(odd) {
						padding-left: 25px;
						margin-left: 50%;
						&:before {
							left: -9.5px;
						}
					}
					&:nth-child(even) {
						padding-right: 25px;
						text-align: right;
						&:before {
							right: -9.5px;
						}
					}
					&:first-child {
						margin-left: 50%;
						margin-top: 0;
					}
					&:last-child {
						&:after {
							content: '';
							display: block;
							position: absolute;
							left: -5px;
							bottom: 0;
							top: 23px;
							width: 10px;
							background-color: #fff;
						}
					}
					&:nth-child(4) ~ li {
						float: none;
						width: 50%;
						&:last-child {
							float: none;
						}
					}
					&.major {
						&:before {
							background-image: repeating-linear-gradient(-45deg, transparent, transparent 1px, rgba(0,170,165,1) 2px, rgba(0,170,165,1) 3px);
						}
					}
				}
				strong {
					display: block;
					color: #48afde;
				}
			}
			.wrap-video{
				video{
					width: 100%;
				}
			}
		}
		.inclusion-program{
			margin-bottom: 35px;
		}
		.inclusion-box{
			padding: 30px 0;
			.inclusion-title{
				width: 100%;
				margin-bottom: 15px;
				small{
					display: inline;
				}
			}
			.inclusion-desc{
				width: 100%;
				&-box{
					width: 100%;
					margin-bottom: 20px;
					padding-left: 15px;
					box-sizing: border-box;
					&:last-child{
						margin-bottom: 0;
					}
					& > strong{
						margin-bottom: 15px;
					}
				}
			}
			.inclusion-program-box{
				margin-bottom: 15px;
				&:last-child{
					margin-bottom: 0;
				}
			}
		}

		.box-wrap-information{
			margin:35px 0;
			padding: 0 20px;
			.section-contents{
				border: 0;
				padding-top: 35px;
				padding-bottom: 70px;
				box-sizing: border-box;
				.box-top
				{
					& > strong{
						font-size: 22px;
					}
					.text {
						font-size: 15px;
					}
				}	
				.box-cont {
					margin-top:40px;
					& > div{
						cursor: pointer;
						& > strong {
							display:block;
							float: none;
							position: relative;
							&:after {
								position: absolute;
								top:50%;
								right: 0;
								margin-right: 6px;
								border-right: 3px solid;
								border-bottom: 3px solid;
								transform: rotate(45deg) translate(-50%,-50%);
								display:block;
								content:"";
								width:6px;
								height:6px;
							}
						}
						& > .btn-info-pc{
							display:none;
						}
					}
					& > div.on{
						& > strong {
							&:after {
								transform: rotate(225deg) translate(0,0);
							}
						}	
					}
					& > .box-info-pc{
						display:none;
					}
					& > .box-info-mo{
						display:block;
					}
					& > .box-info-mo ~ ul{
						display:none;
					}
					& > .box-info-mo.on ~ ul{
						//display:block;
					}
					ul{
						li {
							width:100%;
							float: none;
							margin-left:0;
							height:auto;
							padding-bottom:40px;
							& > i{
								width:37%;
							}
						}
					}
					& > .btn-info-mo{
						float: none;
						width:100%;
						display:inline-flex;
						margin-top:20px;
					}
				}
				.box-cont-2{
					ul{
						li{
							&:nth-of-type(2n+1){
								margin-left: 0px;
							}
							.number-box{
								display:table-cell;
								padding:40px 0 0 40px;
								margin:0;
								width:37%;
								.number{
									transform: translate(80%, -120%);
								}
							}
							&:nth-child(5){
								.number{
									transform: translate(80%, -140%);
								}
							}
							.texts-box{
								padding:40px 30px 0 25px;
								margin:0;
								display:table-cell;
								text-align: left;
							}
						}
					}
				}
			}
		}
		.list {
			&-default {
				margin: 0 0 75px;
				padding: 0 20px;
				box-sizing: border-box;
				border-top: none;
				> li {
					padding: 30px 10px;
					.header {
						float: none;
						width: 100%;
						padding: {
							left: 0;
							right: 0;
							top: 0;
							bottom: 20px;
						}
						.title {
							font-size: 22px;
						}
					}
					.contents {
						float: none;
						width: 100%;
						word-break: break-all;
						p {
							line-height: 1.4;
							+ p {
								margin-top: 25px;
							}
						}
						ul {
							margin-bottom: 25px;
							li {
								position: relative;
								display: block;
								padding: {
									left: 10px;
									top: 5px;
									right: 0;
									bottom: 0;
								};
								border: none;
								&:before {
									content: '-';
									position: absolute;
									left: 0;
								}
							}
						}
					}
					&:first-child {
						border-top: 1px solid #000;
					}
				}

			}
		}
		.process {
			.inner {
				padding: 15px 30px 0;
				box-sizing: border-box;
			}
			h4 {
				margin-bottom: 15px;
				font-size: 22px;
			}
			.con{
				line-height: 1.4;
			}
			ol {
				@include clear;
				margin-top: 30px;
				&:before {
					left: 52px;
					top: 50px;
					right: inherit;
					bottom: 50px;
				}
				> li {
					position: relative;
					float: none;
					padding-left: 0;
					padding-top: 50px;
					text-align: left;
					box-sizing: border-box;
					&:first-child {
						padding-top: 0;
					}
					div {
						display: inline-block;
						width: calc(100% - 115px);
						vertical-align: middle;
						padding: 0 0 0 20px;
						box-sizing: border-box;
						strong {
							font-size: 18px;
							&:before {
								font-size: 15px;
							}
						}
						p {
							margin-top: 5px;
							font-size: 15px;
						}
					}
				}
			}
		}
		.finance-submain{
			h4{
				&.bullet{
					margin-left:0;
					padding-left:0;
				}
			}
			.m_br{
				display:block;
			}
			.pc_br{
				display:none;
			}
		}
		.headline.forum-headline{
			.description{
				.text-center{
					text-align:left;
				}
			}
			.m_br{
				display:block;
			}
			.pc_br{
				display:none;
			}
		}
		.forum-tit{
			.m_br{
				display:block;
			}
			.pc_br{
				display:none;
			}
		}
		.forum-key{
			padding-bottom: 15px;
			h4{
				margin-left:0;
			}
			ol{
				padding:0;
				li{
					float: left;
					text-align: center;
					padding-top:0;
					width:33.3%;
					.icon-business-region-diy-1,
					.icon-business-region-diy-2,
					.icon-business-region-diy-3{
						width:130px;
						height:130px;
					}
					div{
						display:block;
						padding:0;
						margin:10px auto 0;
						width:auto;
					}
				}
				&:before{
					left: 80px;
					top: 70px;
					right: 80px;
					bottom:auto;
					border:1px dashed #ccc;
				}
			}
			.m_br{
				display:block;
			}
			.pc_br{
				display:none;
			}
		}
		.figures.family.forum{
			h5{
				padding-left:0;
			}
			.con{
				padding-left:0;
			}
			.m_br{
				display:block;
			}
			.pc_br{
				display:none;
			}
			ul{
				li{
					strong{
						text-indent: -12px;
						padding-left:12px;
						display:inline-block;
					}
				}
			}
			figure{
				background-size: 120% auto;
    			background-position: 0% 80%;
				.pane{
					span{
						float: left;
						max-width:100%;
						width:50%;
					}
					figcaption{
						transform: translateY(0);
						padding-top: 0;
						left: 50%;
						&:before{
							width:15px;
						}
					}
				}
				.item-1{
					margin-top:30px;
					float:left;
					width:35%;
				}
				.item-2{
					float: left;
					width:45%;
					margin-top:2%;
					margin-left:20px;
					margin-right:0;
				}
				&.forum-list-img-02{
					.pane{
						margin-top:70px;
						figcaption{
							left:auto;
							transform: translateY(-40px);
						}
					}
					.item-1{
						width:45%;
					}
					.item-2{
						width:30%;
					}
				}
			}
			.forum-list-box-03{
				.img{
					.item{
						float: left;
						width:50%;
					}
				}
			}
		}
		.finance.forum-finance.box{
			padding-bottom:0;
			h5{
				padding-right:0;
			}
			.item{
				margin-bottom:15px;
				padding-bottom:15px;
			}
		}
		.figures.information.forum-figures{
			figure{
				background-size: 120% auto;
				background-position: 0% 80%;
				.pane{
					span{
						float: left;
						width:50%;
						display:inline-block;
					}
					figcaption{
						padding-top: 0;
						position: relative;
						display:inline-block;
						&:before{
							width:15px;
						}
					}
				}
				.item-1{
					width:30%;
				}
				.item-2{
					margin-top: 20px;
					margin-left: 20px;
					margin-right:auto;
					float:left;
					width:42%;
				}
				.item-3{
					margin-left:11%;
					margin-right:auto;
				}
			}
		}

		.finance-con {
			h5 {
				padding-top: 40px;
				margin-left: 10px;
			}
			.sub {
				&-title {
					color: #333;
					margin-left: 10px;
				}
				&-explain {
					margin-left: 10px;
				}
			}
			.img-financial-health {
				position: static;
				display: block;
				margin: 30px 0 0;
				text-align: center;
				top: auto;
				bottom: 0;
			}
			.standards {
				padding-bottom: 50px;
				&.new {
					display: flex;
					flex-direction: row;
					flex-wrap: wrap;
					justify-content: stretch;
					align-content: stretch;
					align-items: flex-start;

					&:after{
						content: none;
					}
					li {
						float: none;
						display: inline-block;
						flex: 0 1 33.3333%;
						align-self: auto;
						vertical-align: top;
						border-radius: 15px;
						width: auto;
						height: auto;
						padding: 5px;
						text-align: center;
						margin: 0;
						.item {
							&-wrap {
								width: 100%;
								padding: 36px 12px 12px;
								margin: 0 auto 0;
								background-color: #fff;
								border-radius: 15px;
								box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, .06);
								div {
									width: 100%;
									min-height: 140px;
									height: 100%;
								}
							}
						}
						strong, span {
							padding: 0;
						}
						strong {
							display: block;
							padding: 25px 0 10px;
							position: relative;
							&:before {
								content: '';
								position: absolute;
								top: -25px;
								left: 50%;
								transform: translateX(-50%);
							}
						}
						span {
							font: {
								size: 12px;
							}
							line-height: 1.3;
						}
						&.danger {
							strong {
								&:before {
									@include m-sprite($m-face-danger);
								}
							}
						}
						&.weak {
							strong {
								&:before {
									@include m-sprite($m-face-weak);
								}
							}
						}
						&.caution {
							strong {
								&:before {
									@include m-sprite($m-face-caution);
								}
							}
						}
						&.good {
							strong {
								&:before {
									@include m-sprite($m-face-good);
								}
							}
						}
						&.excellent {
							strong {
								&:before {
									@include m-sprite($m-face-excellent);
								}
