/*! introduce */
@media (max-width:$medium-devices) {
	.vision{
		padding-top: 50px;
		.headline{
			padding: 0 20px;
			font-size: 18px;
		}
		h3 {
			margin-bottom: 20px;
			padding: 10px 45px;
			font-size: 27px;
		}
		h4 {
			width: 209px;
			height: 40px;
			margin-bottom: 0;
			padding: 0;
			line-height: 37px;
			font-size: 15px;
			background-color: #fff;
		}
		.box {
			&-mission{
				margin-top: 50px;
				margin-bottom: 0;
				padding: 0 20px 60px;
				font-size: 18px;
				&:after{
					top: 100%;
					bottom: inherit;
					height: 50px;
					transform: translateY(-50%);
				}
			}
			&-strategic{
				.items{
					margin: 0;
				}
				.item{
					padding: 0 35px 45px;
					&:after{
						display: none;
					}
				}
			}
			&-activities{
				&:before,
				&:after{
					content: none;
				}
				.items{
					padding: 0;
					margin-top: 0;
					border: none;
					background-color: transparent;
					box-shadow: none;
				}
				.item{
					padding: 35px 35px 45px;
				}
			}
		}
		.box-group{
			padding: {
				top: 45px;
				left: 20px;
				right: 20px;
				bottom: 50px;
			};
			background-image: none;
			box-sizing: border-box;
			.box{
				margin: 0 auto 50px;
			}
		}
		.item {
			border-width: 4px;
			font-size: 20px;
			> span {
				height: auto;
				padding-top: 45px;
				margin-bottom: 25px;
			}
			.icon {
				//transform: scale(0.8);
				vertical-align: bottom;
			}
			.title {
				display: block;
				margin-bottom: 10px;
				padding-top: 0;
				font-weight: 700;
				font-size: 24px;
			}
			ul {
				padding: 0;
				box-sizing: border-box;
			}
			li {
				margin-bottom: 15px;
				padding-left: 15px;
				font-size: 16px;
				&:before {
					content: '';
					display: inline-block;
					position: absolute;
					left: 0;
					top: 8px;
					width: 4px;
					height: 4px;
					border-radius: 50%;
				}
			}
		}
		.items{
			@include clear;
			margin: 0;
			.item{
				float: none;
				width: 100%;
				min-height: 245px;
				height: auto;
				margin: 25px 0 0 0;
				box-sizing: border-box;
				box-shadow: 8px 10px 25px 0px #e5e5e5;
				&-financial{
					border-top: 4px solid #58c1f7;
				}
				&-healthy{
					border-top: 4px solid #a4ce4e;
				}
				&-culture{
					border-top: 4px solid #0061a0;
				}
				&-envirement{
					border-top: 4px solid #00aca0;
				}
			}
		}
	}

	.greeting{
		.headline{
			padding: 45px 20px;
			font-size: 22px;
			letter-spacing: -.5px;
		}
		.contents{
			padding: 20px;
			font-size: 15px;
			p{
				word-break: break-all;
			}
		}
		.img{
			width: 100%;
			margin-bottom: 75px;
			padding: 0 20px;
			box-sizing: border-box;
		}
	}

	.director{
		padding-top: 25px;
		padding-bottom: 70px;
		.item{
			padding: 25px 20px;
			.img{
				float: none;
				width: 100%;
				height: auto;
				img{
					position: static;
					width: 100%;
					max-width: 100%;
					height: auto;
					transform: none;
				}
			}
			.contents{
				float: none;
				width: 100%;
				padding: {
					top: 20px;
					left: 0;
				};
				font-size: 15px;
				strong,
				span{
					font-size: 18px;
					letter-spacing: -1px;
				}
				p{
					margin-top: 20px;
					letter-spacing: 0;
					word-break: break-all;
				}
			}
		}
	}

	.history{
		padding-bottom: 50px;
		.list-history{
			margin: 0 20px;
			& > li{
				padding: 50px 0 25px;
				&:before{
					bottom: -10px
				}
				.img{
					float: none;
					width: 100%;
					height: auto;
					img{
						position: static;
						max-width: 100%;
						height: auto;
						transform: none;
					}
				}
				.contents{
					float: none;
					width: 100%;
					padding-left: 10px;
					strong{
						margin-top: 20px;
						font-size: 40px;
					}
					ul li{
						padding: 0 0 0 10px;
						border-bottom: 0;
						&:last-child{
							margin-bottom: 0;
						}
					}
				}
			}
		}
	}

	.contribution{
		margin: 0 20px;
		padding-bottom: 50px;
		.table {
			table{
				margin: 35px 0;
			}
			thead th{
				padding: 10px 0;
				strong{
					padding-left: 10px;
					font-size: 22px;
				}
				span{
					font-size: 15px;
				}
			}
			tbody td{
				padding: 30px 15px;
				.summary{
					strong,
					span{
						font-size: 15px;
					}
					strong{
						float: left;
					}
				}
				ul{
					margin-top: 15px;
					li{
						strong,
						span{
							font-size: 15px;
						}
						strong{
							letter-spacing: -1px;
						}
					}
				}

			}
			tfoot td{
				padding-right: 15px;
				strong{
					padding-left: 24px;
					font-size: 15px;
				}
				span{
					font-size: 15px;
				}
			}
		}
	}

	.contact{
		padding-bottom: 30px;
		.wrap-map{
			margin: 25px 20px 0;
		}
		.location-info{
			ul{
				margin: 0 20px;
				padding-top: 5px;
			}
			li{
				display: table;
				*{
					display: table-cell;
					font-size: 15px;
					vertical-align: top;
				}
				em{
					max-width: 80px;
					padding-left: 5px;
					box-sizing: border-box;
				}
				a{
					display: inline;
				}
			}
		}
	}

	.groups {
		.headline {
			margin-top: 60px;
			span {
				display: inline;
				font-size: 35px;
			}
			b {
				display: inline;
				font-size: 35px;
			}
			.img {
				width: auto;
				padding:0 20px;
				margin: 30px auto 0;
			}
			.text {
				padding:0 20px;
			}
		}
		.box-wrap {
			margin-top: 35px;
			padding: 0 20px;
			> div {
				padding-top: 35px;
			}
			.box-top {
				strong {
					padding-left: 17px;
					font-size: 22px;					
				}
				.text {
					margin-top: 15px;
					font-size: 15px;
				}
			}
			.box-cont {
				margin-top:40px;
				strong {
					font-size: 18px;
				}
				ul {
					@include clear;
				}
				li {
					float: left;
					width: 49%;
					height: 180px;
					background-color: #fff;
					margin-left: 2%;
					margin-top: 24px;
					border-bottom:3px solid #ccc;
					&.col1, &.col3, &.col5 {
						margin-left: 0px;
					}
					&.col4, &.col6 {
						margin-left: 2%;
					}
					.numbers {
						margin-top: 10px;
						strong {
							font-size: 50px;
							position: relative;
						}
						span {
							color:#878787;
							font-weight: bold;
							font-size: 14px;
							position: relative;
							left: -5px;
						}
						&.plus strong:after {
							display: block;
							content: "+";
							position: absolute;
							top: -5px;
							right: -25px;
							font-size: 30px;
						}
					}
					&.col1 {
						.numbers strong {
							color: #0090da;
						}
					}
					
	
					> p {
						font-weight: bold;
						font-size: 15px;
					}
					> em {
						display: block;
						margin-top: 5px;
						font-size: 12px;
					}
	
				}
			}
	
	
	
			+ .box-wrap {
				background-color: #f7f7f7;
				> div {
					border:0;
					padding-bottom: 70px;
				}
	
				.box-cont {
					li{
						border-bottom:0;
						padding-left: 25px;
						box-sizing: border-box;
						-webkit-box-shadow: 8px 10px 25px 0px #e5e5e5;
						box-shadow: 8px 10px 25px 0px #e5e5e5;
						.numbers {
							margin-top: 20px;
						}
					}
				}
			}
		}
	}

}
@media (max-width:$small-devices) {

	.groups .box-wrap .box-cont li .numbers strong {font-size: 40px;}
	.groups .box-wrap + .box-wrap .box-cont li {height: 160px;}
}