/*! header */
@media (max-width:$medium-devices) {
	.header{
		// padding-top: 80px;
		&-top{
			height: 80px;
			padding-top: 25px;
			h1{
				a{
					&:before{
						content: '';
						display: inline-block;
						width: 250px;
						padding-bottom: 11.4%;
						background-image: url('../images/m-logo.png');
						background-size: 100% 100%;
					}
					img{
						display: none;
					}
				}
			}
			.btn-home{
				display: none;
			}
			.util{
				top: 0;
				right: 10px;
				bottom: 0;
				em,
				.btn-mypage{
					display: none;
				}
				.btn-log-inout{
					top: 0;
					height: 100%;
					margin: 0;
					padding: 0 10px;
					@include align-middle;
				}
			}
		}
		&-megamenu{
			.btn-megamenu{
				top: 0;
				left: 10px;
				height: 100%;
				padding: 10px;
				.line{
					width: 21px;
				}
				&-text{
					display: none;
				}
			}

			.megamenu {
				&-menulist {
					top: 80px;
				}
				&-menubox {
					border-top: 0;
					&-list1 {
						float: none;
						width: 100%;
						padding: 0;
						a {
							line-height: 90px;
							display: block;
							height: 90px;
							padding: {
								left: 28px;
							};
							border-bottom: 1px solid #ccc;
						}
					}
				}
				&-dropdown {
					overflow: hidden;
					height: 0;
					padding: 0;
					background-color: #fff;
					transition: height 0.3s;
					li {
						a {
							line-height: 75px;
							display: block;
							height: 75px;
							border-bottom: 0;
						}
					}
				}
			}
		}
		&.fixed{
			.header-top{
				.util {
					top: 0;
				}
				.btn-megamenu{
					top: 0;
				}
			}
		}
		.gnb-wrap {
			height: auto;
			max-height: 55px;
			ul {
				display: block;
			}
			.gnb {
				display: none;
				&:hover {
					.depth-1 {
						background-color: #0061a0;
						&:after {
							display: none;
						}
					}
				}
				.depth-1 {
					position: relative;
					display: block;
					width: auto;
					height: 55px;
					line-height: 55px;
					&:before {
						display: none;
					}
					a {
						height: 55px;
						padding-left: 20px;
						font-size: 15px * 1.5;
						text-align: left;
						&:before,
						&:after{
							content: '';
							position: absolute;
							right: 20px;
							display: block;
							width: 0;
							height: 0;
							border-style: solid;
						}
						&:before{
							bottom: 50%;
							margin-bottom: 2px;
							border-width: 0 6px 8px 6px;
							border-color: transparent transparent #fff transparent;
						}
						&:after{
							top: 50%;
							margin-top: 2px;
							border-width: 8px 6px 0 6px;
							border-color: #fff transparent transparent transparent;
						}
					}
				}
				.depth-2 {
					position: relative;
					overflow: hidden;
					display: block;
					max-height: 0;
					height: auto;
					background-color: #f3f2f3;
					box-shadow: 0 5px 10px 0 rgba(162, 162, 162, 0.4);
					transition: max-height 0.3s;
					ul {
						display: block;
						width: auto;
						max-width: none;
						padding-left: 0;
					}
					li {
						display: block;
						float: none;
						flex: none;
						max-width: none;
						width: 100%;

					}
					a {
						width: auto;
						height: 55px;
						padding: 0 20px;
						font-size: 19px;
						font-weight: 400;
						color: #333;
						text-align: left;
						white-space: nowrap;
						overflow: hidden;
						&:hover{
							background-color: transparent;
						}
						&:after {
							content: '';
							display: inline-block;
							height: 100%;
							vertical-align: middle;
						}
						span {
							display: inline-block;
							padding-top: 0;
							color: #333;
							letter-spacing: -.5px;
							vertical-align: middle;
						}
					}
					em {
						display: none;
					}
				}
				&.show {
					.depth-2 {
						max-height: 500px;
					}
				}
				&.active {
					display: block;
					float: none;
				}
			}
			.gnb-5.active{
				.depth-1{
					a:after{
						display: none;
					}
				}
			}
		}
	}
}

@media (max-width:$small-devices) {
	.header{
		&-top{
			padding-top: 28px;
		}
		h1{
			a:before{
				width: 200px;
			}
		}
	}
}